import countdown1 from './components/countdown1.vue';
export default {
  components: {
    countdown1: countdown1
  },
  data: function data() {
    return {
      day: null,
      hour: null,
      min: null,
      sec: null
    };
  },
  mounted: function mounted() {
    var _this = this;

    setInterval(function () {
      var graduation = new Date(2020, 2, 4, 14, 25);
      var now = new Date();
      var until = graduation - now;
      _this.day = Math.floor(until / (1000 * 60 * 60 * 24));
      until -= _this.day * (1000 * 60 * 60 * 24);
      _this.hour = Math.floor(until / (1000 * 60 * 60));
      until -= _this.hour * (1000 * 60 * 60);
      _this.min = Math.floor(until / (1000 * 60));
      until -= _this.min * (1000 * 60);
      _this.sec = Math.floor(until / 1000);
    }, 1000);
  }
};